/* Works on Chrome, Edge, and Safari */
[data-theme=zwingli] {
	--bg-color:  #e5e8eb;
	--btn-primary: #3F51B5;
	--btn-secondary: #017E8480;
	--secondary: #017E84;
	--navbar-bg: #3F51B5;
	--text-btn-color: white;
	--table-border-color: #714b6740;
	--btn-default-hover-bg: white;
	--sidebar-select-color: #017E8420;
	--checkbox-gradient: linear-gradient(180deg, var(--btn-primary) -124.51%, var(--btn-primary) 100%);
	--text-base: 14px;
}


/* nav colors start */
[data-theme=zwingli] .navbar-nav>li>a{
    color:#fff !important;
}
[data-theme=zwingli] .navbar-nav>li>a:hover{
    color:#ddd !important;
}
[data-theme=zwingli] .navbar-brand:hover {
    color:#ddd !important;
}

[data-theme="zwingli"] .navbar-default .navbar-nav > li > a {
	color: #fff;
}
[data-theme="zwingli"] .navbar-default .navbar-nav > li > a:hover {
	color: #ddd;
}
[data-theme="zwingli"] .navbar-default .navbar-brand:hover {
	color: #ddd;
}

[data-theme=zwingli] #navbar-breadcrumbs > li > a::before {
    font-family: FontAwesome;
    font-weight: normal;
    font-style: normal;
    text-decoration: inherit;
    -webkit-font-smoothing: antialiased;
    margin-right: .3em;
    display: inline-block;
    font-size: 24px;
    transition: 0.2s;
    position: relative;
    top: 3px;
    content: "\f105";
    margin-right: 10px;
    color: var(--white-color);
}
/* end */
